import React from "react";

function MessageSelf({ props }) {
  var createdAtValue = props?.createdAt;

  var datePart = createdAtValue?.split("T")[0];
  var timePart = createdAtValue?.split("T")[1].split(".")[0].slice(0, -3);

  return (
    <div className="self-message-container">
      <div className="messageBox">
        <p className="con-lastMessage" style={{ color: "black" }}>
          {props.content}
        </p>
        <p className="self-timeStamp" style={{ color: "black" }}>
          <span className="date">{datePart}</span>
          <span className="time">{timePart}</span>
        </p>
      </div>
    </div>
  );
}

export default MessageSelf;
