import React, { useContext, useEffect, useState } from "react";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import { IconButton } from "@mui/material";
import PersonAddIcon from "@mui/icons-material/PersonAdd";
import GroupAddIcon from "@mui/icons-material/GroupAdd";
import NightlightIcon from "@mui/icons-material/Nightlight";
import LightModeIcon from "@mui/icons-material/LightMode";
import ExitToAppIcon from "@mui/icons-material/ExitToApp";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import SearchIcon from "@mui/icons-material/Search";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toggleTheme } from "../features/themeSlice";
import axios from "axios";
import { refreshSidebarFun } from "../features/refreshSidebar";
import { myContext } from "./MainContainer";
import io from "socket.io-client";

const socket = io("https://chatapp-ht.onrender.com");

function Sidebar() {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const lightTheme = useSelector((state) => state.themeKey);
  const { refresh, setRefresh } = useContext(myContext);
  const refreshSb = useSelector((state) => state.refreshKey);
  const [conversations, setConversations] = useState([]);
  const [tempConversations, setTempConversations] = useState([]);
  const userData = JSON.parse(localStorage.getItem("userData"));
  const nav = useNavigate();
  const [searchInput, setSearchInput] = useState("");

  if (!userData) {
    nav("/");
  }

  useEffect(() => {
    if (searchInput === "") {
      setTempConversations([...conversations]);
    }
  }, [searchInput]);

  const searchPerson = () => {
    if (!searchInput) {
      setTempConversations([...conversations]);
      return;
    }
    const lowerCaseSearchInput = searchInput.toLowerCase();
    const filteredConversations = tempConversations?.filter((user) => {
      if (user.isGroupChat) {
        const lowerCaseSenderName = user?.chatName.toLowerCase();
        return lowerCaseSearchInput === lowerCaseSenderName;
      } else {
        const lowerCaseSenderName = user?.users;
        const second = lowerCaseSenderName[1].name;
        return lowerCaseSearchInput === second;
      }
    });
    setTempConversations([...filteredConversations]);
  };

  const handleValue = (e) => {
    setSearchInput(e.target.value);
  };

  useEffect(() => {
    const config = {
      headers: {
        Authorization: `Bearer ${userData?.token}`,
      },
    };

    axios
      .get("https://chatapp-ht.onrender.com/chat/", config)
      .then((response) => {
        setConversations(response.data);
        setTempConversations(response.data);
        // setRefresh(!refresh);
      })
      .catch((error) => {
        console.error("Sidebar Error:", error);
      });

    // Handle "refreshSidebar" event
    socket.on("refreshSidebar", () => {
      setRefresh(!refresh);
    });
  }, [refresh, refreshSb]);

  return (
    <div className="sidebar-container">
      <div className={"sb-header" + (lightTheme ? "" : " dark")}>
        <div className="other-icons">
          <IconButton
            onClick={() => {
              nav("/app/welcome");
            }}
          >
            <AccountCircleIcon
              className={"icon" + (lightTheme ? "" : " dark")}
            />
          </IconButton>

          <div>
            <IconButton
              onClick={() => {
                navigate("users");
              }}
            >
              <PersonAddIcon className={"icon" + (lightTheme ? "" : " dark")} />
            </IconButton>
            <IconButton
              onClick={() => {
                navigate("groups");
              }}
            >
              <GroupAddIcon className={"icon" + (lightTheme ? "" : " dark")} />
            </IconButton>
            <IconButton
              onClick={() => {
                navigate("create-groups");
              }}
            >
              <AddCircleIcon className={"icon" + (lightTheme ? "" : " dark")} />
            </IconButton>

            <IconButton
              onClick={() => {
                dispatch(toggleTheme());
              }}
            >
              {lightTheme && (
                <NightlightIcon
                  className={"icon" + (lightTheme ? "" : " dark")}
                />
              )}
              {!lightTheme && (
                <LightModeIcon
                  className={"icon" + (lightTheme ? "" : " dark")}
                />
              )}
            </IconButton>
            <IconButton
              onClick={() => {
                localStorage.removeItem("userData");
                navigate("/");
              }}
            >
              <ExitToAppIcon className={"icon" + (lightTheme ? "" : " dark")} />
            </IconButton>
          </div>
        </div>
      </div>

      <div className={"sb-search" + (lightTheme ? "" : " dark")}>
        <IconButton className={lightTheme ? "" : " dark"}>
          <SearchIcon />
        </IconButton>
        <input
          placeholder="Search"
          className={"search-box" + (lightTheme ? "" : " dark")}
          onChange={handleValue}
          onKeyDown={(event) => {
            if (event.code === "Enter") {
              searchPerson();
            }
          }}
        />
      </div>
      <div className={"sb-conversations" + (lightTheme ? "" : " dark")}>
        <ul className="temp">
          {tempConversations.map((conversation, index) => {
            var chatName = "";
            if (conversation.isGroupChat) {
              chatName = conversation.chatName;
            } else {
              conversation.users.map((user, i) => {
                if (user?._id !== userData._id) {
                  chatName = user.name;
                }
              });
            }

            if (conversation.latestMessage === undefined) {
              return (
                <div
                  key={index}
                  onClick={() => {
                    setRefresh(!refresh);
                  }}
                >
                  <div
                    key={index}
                    className="conversation-container main-con-container"
                    onClick={() => {
                      navigate("chat/" + conversation._id + "&" + chatName);
                    }}
                  >
                    <p className={"con-icon" + (lightTheme ? "" : " dark")}>
                      {chatName[0]}
                    </p>
                    <p className={"con-title" + (lightTheme ? "" : " dark")}>
                      {chatName}
                    </p>

                    <p
                      className={
                        "con-lastMessage" + (lightTheme ? "" : " dark")
                      }
                    >
                      No previous Messages, click here to start a new chat
                    </p>
                    <p
                      className={"con-timeStamp" + (lightTheme ? "" : " dark")}
                    >
                      {conversation?.latestMessage?.createdAt}
                    </p>
                  </div>
                </div>
              );
            } else {
              const createdAtValue = conversation?.latestMessage?.createdAt;

              const datePart = createdAtValue.split("T")[0];
              const timePart = createdAtValue
                .split("T")[1]
                .split(".")[0]
                .slice(0, -3);

              return (
                <div
                  key={index}
                  className={
                    "conversation-container main-con-container" +
                    (lightTheme ? "" : " dark")
                  }
                  onClick={() => {
                    navigate("chat/" + conversation._id + "&" + chatName);
                  }}
                >
                  <p className={"con-icon" + (lightTheme ? "" : " dark")}>
                    {chatName[0]}
                  </p>
                  <p className={"con-title" + (lightTheme ? "" : " dark")}>
                    {chatName}
                  </p>

                  {/* <p className="con-lastMessage"> */}
                  <p
                    className={"con-lastMessage" + (lightTheme ? "" : " dark")}
                  >
                    {conversation.latestMessage.content}
                  </p>
                  <p className={"con-timeStamp" + (lightTheme ? "" : " dark")}>
                    {/* {conversation?.latestMessage?.createdAt} */}
                    {datePart} {timePart}
                  </p>
                </div>
              );
            }
          })}
        </ul>
      </div>
    </div>
  );
}

export default Sidebar;
