import React, { useEffect } from "react";
import logo from "../social.png";
import { useSelector } from "react-redux";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";

function Welcome() {
  const lightTheme = useSelector((state) => state.themeKey);
  const userData = JSON.parse(localStorage.getItem("userData"));
  // console.log("In welcome : ", "User data has been retrieved : ", userData);
  const nav = useNavigate();

  // if (!userData) {
  //   console.log("User not Authenticated");
  //   nav("/");
  //   return null;
  // }

  useEffect(() => {
    if (!userData) {
      // console.log("In welcome : ", "User not Authenticated");
      nav("/");
    }
    // console.log("In welcome : ", userData, " Updated");
  }, [userData, nav]);

  // console.log("In welcome : ", "We are not here");

  return (
    <div className={"welcome-container" + (lightTheme ? "" : " dark")}>
      <motion.img
        drag
        whileTap={{ scale: 1.05, rotate: 360 }}
        src={logo}
        alt="Logo"
        className="welcome-logo"
      />
      <b>Hi , {userData.name} 👋</b>
      <p>View and text directly to people present in the chat Rooms.</p>
      {/* {userData} */}
    </div>
  );
}

export default Welcome;
