import React, { useContext, useEffect, useRef, useState } from "react";
import DeleteIcon from "@mui/icons-material/Delete";
import { IconButton } from "@mui/material";
import SendIcon from "@mui/icons-material/Send";
import MessageSelf from "./MessageSelf";
import MessageOthers from "./MessageOther";
import { useDispatch, useSelector } from "react-redux";
import { useParams, useNavigate } from "react-router-dom";
import Skeleton from "@mui/material/Skeleton";
import axios from "axios";
import { myContext } from "./MainContainer";
import io from "socket.io-client";

const ENDPOINT = "https://chatapp-ht.onrender.com";
let socket, chat;

function ChatArea() {
  const lightTheme = useSelector((state) => state.themeKey);
  const [messageContent, setMessageContent] = useState("");
  const messagesEndRef = useRef(null);
  const dyParams = useParams();
  const [chat_id, chat_user] = dyParams._id.split("&");
  const userData = JSON.parse(localStorage.getItem("userData"));
  const [allMessages, setAllMessages] = useState([]);
  const [allMessagesCopy, setAllMessagesCopy] = useState([]);

  // const refresh = useSelector((state) => state.refreshKey);
  const { refresh, setRefresh } = useContext(myContext);
  const [loaded, setloaded] = useState(false);
  const [socketConnectionStatus, setSocketonnectionStatus] = useState(false);

  const navigate = useNavigate();

  const scrollToBottom = () => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({
        behavior: "auto",
        block: "end",
      });
    }
  };

  const removeUserFromGroup = () => {
    const config = {
      headers: {
        Authorization: `Bearer ${userData.token}`,
      },
    };

    const requestData = {
      chatId: chat_id,
      userId: userData._id, // ID of the user to be removed
    };
    axios
      .put(
        "https://chatapp-ht.onrender.com/chat/groupExit",
        requestData,
        config
      )
      .then(({ data }) => {
        navigate("/app/welcome");
      })
      .catch((error) => {
        console.error("Error removing user from group chat:", error);
      });
  };
  //  send message functipn
  const sendMessage = () => {
    const trimmedMessageContent = messageContent.trim();

    // Check if the trimmed message content is empty
    if (trimmedMessageContent === "") {
      // If it's empty, return without sending the message
      return;
    }
    const config = {
      headers: {
        Authorization: `Bearer ${userData.token}`,
      },
    };

    axios
      .post(
        "https://chatapp-ht.onrender.com/message/",
        {
          content: messageContent,
          chatId: chat_id,
        },
        config
      )
      .then(({ data }) => {
        socket.emit("newMessage", data);
        socket.emit("refreshSidebar");
      });
  };

  //  fetch message
  useEffect(() => {
    const config = {
      headers: {
        Authorization: `Bearer ${userData.token}`,
      },
    };
    axios
      .get("https://chatapp-ht.onrender.com/message/" + chat_id, config)
      .then(({ data }) => {
        setAllMessages(data);
        setloaded(true);
        // socket.emit("join chat", chat_id);
      });
    setAllMessagesCopy(allMessages);
  }, [chat_id, userData.token, refresh]);

  // connect to socket
  useEffect(() => {
    socket = io(ENDPOINT);
    socket.emit("setup", userData);
    socket.on("connection", () => {
      setSocketonnectionStatus(!socketConnectionStatus);
    });
  }, []);

  // new message received
  useEffect(() => {
    socket.on("messageReceived", (newMessage) => {
      let chatProcessed = false;

      const isChatPresent = allMessages.some(
        (message) => message.chat._id === newMessage.chat._id
      );
      if (!isChatPresent && !chatProcessed) {
        setAllMessages((prevMessages) => [...prevMessages, newMessage]);
        scrollToBottom();
        chatProcessed = true;
      }
    });
  }, []);

  // useEffect(() => {
  //   let chatProcessed = false;

  //   const handleNewMessage = (newMessage) => {
  //     if (!chatProcessed) {
  //       setAllMessages((prevMessages) => [...prevMessages, newMessage]);
  //       scrollToBottom();
  //       chatProcessed = true;
  //     }
  //   };

  //   socket.on("messageReceived", handleNewMessage);

  //   return () => {
  //     // Clean up the event listener when the component unmounts
  //     socket.off("messageReceived", handleNewMessage);
  //   };
  // }, []);

  // fecth chats

  useEffect(() => {
    const config = {
      headers: {
        Authorization: `Bearer ${userData.token}`,
      },
    };
    axios
      .get("https://chatapp-ht.onrender.com/message/" + chat_id, config)
      .then(({ data }) => {
        setAllMessages(data);
        setloaded(true);
        socket.emit("join chat", chat_id);
        setAllMessagesCopy(data);
        scrollToBottom();
      });
  }, [refresh, chat_id, userData.token]);

  useEffect(() => {
    scrollToBottom();
  }, [allMessages]);

  if (!loaded) {
    return (
      <div
        style={{
          border: "20px",
          padding: "10px",
          width: "100%",
          display: "flex",
          flexDirection: "column",
          gap: "10px",
        }}
      >
        <Skeleton
          variant="rectangular"
          sx={{ width: "100%", borderRadius: "10px" }}
          height={60}
        />
        <Skeleton
          variant="rectangular"
          sx={{
            width: "100%",
            borderRadius: "10px",
            flexGrow: "1",
          }}
        />
        <Skeleton
          variant="rectangular"
          sx={{ width: "100%", borderRadius: "10px" }}
          height={60}
        />
      </div>
    );
  } else {
    return (
      <div className={"chatArea-container" + (lightTheme ? "" : " dark")}>
        <div className={"chatArea-header" + (lightTheme ? "" : " dark")}>
          <p className={"con-icon" + (lightTheme ? "" : " dark")}>
            {chat_user[0]}
          </p>
          <div className={"header-text" + (lightTheme ? "" : " dark")}>
            <p className={"con-title" + (lightTheme ? "" : " dark")}>
              {chat_user}
            </p>
            {/* <p className={"con-timeStamp" + (lightTheme ? "" : " dark")}>
              {props.timeStamp}
            </p> */}
          </div>
          <IconButton
            onClick={removeUserFromGroup}
            className={"icon" + (lightTheme ? "" : " dark")}
          >
            <DeleteIcon />
          </IconButton>
        </div>
        <div className={"messages-container" + (lightTheme ? "" : " dark")}>
          {allMessages
            .slice(0)
            //.reverse()
            .map((message, index) => {
              const sender = message.sender;
              const self_id = userData._id;
              if (sender._id === self_id) {
                return <MessageSelf props={message} key={index} />;
              } else {
                return <MessageOthers props={message} key={index} />;
              }
            })}
          <div ref={messagesEndRef} />
        </div>
        {/* <div ref={messagesEndRef} className="BOTTOM" /> */}

        <div className={"text-input-area" + (lightTheme ? "" : " dark")}>
          <input
            placeholder="Type a Message"
            className={"search-box" + (lightTheme ? "" : " dark")}
            value={messageContent}
            onChange={(e) => {
              setMessageContent(e.target.value);
            }}
            onKeyDown={(event) => {
              if (event.code === "Enter") {
                sendMessage();
                setMessageContent("");
                setRefresh(!refresh);
              }
            }}
          />
          <IconButton
            className={"icon" + (lightTheme ? "" : " dark")}
            onClick={() => {
              sendMessage();
              setRefresh(!refresh);
            }}
          >
            <SendIcon />
          </IconButton>
        </div>
      </div>
    );
  }
}

export default ChatArea;
